import {
  Menubar,
  MenubarContent,
  MenubarItem,
  MenubarMenu,
  MenubarTrigger,
} from "@/components/ui/menubar";
import { useAuth } from "../hooks/useAuth";
import { Button } from "@/components/ui/button";
import { Avatar, AvatarImage, AvatarFallback } from "@radix-ui/react-avatar";

export const AuthMenu = () => {
  const { user, isAuthenticated, getUserInitials, signIn, signOut } = useAuth();

  if (isAuthenticated && user) {
    return (
      <Menubar className="rounded-full border-none">
        <MenubarMenu>
          <MenubarTrigger className="cursor-pointer p-0 focus:bg-transparent data-[state=open]:bg-transparent">
            <Avatar>
              <AvatarImage
                src={user.profile_picture ?? undefined}
                alt={user.name ?? "User"}
              />
              <AvatarFallback>{getUserInitials()}</AvatarFallback>
            </Avatar>
          </MenubarTrigger>
          <MenubarContent className="mr-2 mt-1 min-w-[200px]">
            <div className="px-2 py-1.5 text-sm font-medium">{user.email}</div>
            <MenubarItem
              className="cursor-pointer focus:bg-red-50 focus:text-red-500"
              onClick={signOut}
            >
              Sign out
            </MenubarItem>
          </MenubarContent>
        </MenubarMenu>
      </Menubar>
    );
  }

  return (
    <Button variant="outline" onClick={signIn}>
      Sign In
    </Button>
  );
};
